import {Toast} from 'vant'
import area from '../../common/js/area.js'
import model from '../../components/model/model.vue'
import {airport} from '../../common/js/airport.js'
import {ajax} from '../../common/js/util.js'
export default {
	data() {
		return {
			Color: '#DE2910',
			value: '',
			backColor: '#FFFFFF',
			date: '',
			day: '',
			area: area,
			show1: false,
			show2: false,
			show3: false,
			show4: false,
			start: '',
			end: '',
			index: 0,
			currentTime: new Date(),
			minDate: new Date(),
			listData: [],
			startCode: '',
			endCode: '',
			count: ''
		}
	},
	components: {
		model,
		Toast
	},
	onLoad() {
		//刷新保留用户uid
		if (this.getRequest('uid')) {
			var uids = this.getRequest('uid')
			this.$store.commit('changeUid', uids)
		}
		var myDate = new Date()
		this.date = myDate.getFullYear() + '-' + this.addZero(myDate.getMonth() + 1) + '-' + this.addZero(myDate.getDate())
		this.day = this.getDay(myDate.getDay())
	},
	onShow() {
		this.start = this.$store.state.city3
		this.end = this.$store.state.city4
	},
	onReady() {
		
	},
	methods: {
		getRequest(variable) {
			var query = window.location.search.substring(1);
			var vars = query.split("&");
			for (var i = 0; i < vars.length; i++) {
				var pair = vars[i].split("=");
				if (pair[0] == variable) {
					return pair[1];
				}
			}
			return (false);
		},
		goRoom() {

		},
		addZero(str) {
			str = str > 9 ? str : '0' + str
			return str
		},
		dateFtt(fmt, date) { //author: meizz   
			var o = {
				"M+": date.getMonth() + 1, //月份   
				"d+": date.getDate(), //日   
				"h+": date.getHours(), //小时   
				"m+": date.getMinutes(), //分   
				"s+": date.getSeconds(), //秒   
				"q+": Math.floor((date.getMonth() + 3) / 3), //季度   
				"S": date.getMilliseconds() //毫秒   
			}
			if (/(y+)/.test(fmt))
				fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length))
			for (var k in o)
				if (new RegExp("(" + k + ")").test(fmt))
					fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)))
			return fmt
		},
		getDay(day) {
			switch (day) {
				case 0:
					day = "星期天"
					break
				case 1:
					day = "星期一"
					break
				case 2:
					day = "星期二"
					break
				case 3:
					day = "星期三"
					break
				case 4:
					day = "星期四"
					break
				case 5:
					day = "星期五"
					break
				case 6:
					day = "星期六"
			}
			return day
		},
		choiceStart() {
			uni.navigateTo({
				url: '../selectCity/selectCity?type=3',
				animationType: 'slide-in-left',
				animationDuration: 500
			})
		},
		confirm1(e) {
			this.start = e[1].name
			this.show1 = false
		},
		cancel1() {
			this.show1 = false
		},
		choiceEnd() {
			uni.navigateTo({
				url: '../selectCity/selectCity?type=4',
				animationType: 'slide-in-left',
				animationDuration: 500
			})
		},
		confirm2(e) {
			this.end = e[1].name
			this.show2 = false
		},
		cancel2() {
			this.show2 = false
		},
		cancel() {
			this.show3 = false
			
		},
		confirm(e) {
			this.show3 = false
			console.log(e)
			this.date = this.dateFtt('yyyy-MM-dd', e)
			this.day = this.getDay(new Date(this.date).getDay())
		},
		goList() {
			uni.navigateTo({
				url: '../airList/airList'
			})
		},
		goNum() {
			this.show3 = true
		},
		goLook() {
			if (this.start == '选择出发城市') {
				Toast('请选择出发城市')
				return
			}
			if (this.start == '选择到达城市') {
				Toast('请选择到达城市')
				return
			}
			if(this.$store.state.searchState == 2){
				for(let j=0;j<airport.RECORDS.length;j++){
					if(this.start == airport.RECORDS[j].cityname){
						this.$store.commit('changeStartCity1', airport.RECORDS[i].citycode)
					}
					if(this.end == airport.RECORDS[j].cityname){
						this.$store.commit('changeEndCity1', airport.RECORDS[i].citycode)
					}
				}
			}
			
			let self = this
			this.show4 = true
			let datas = {
				depcity: this.$store.state.startCity1,
				arrcity: this.$store.state.endCity1,
				date: this.date
			}
			let data = {
				url: '/api/gzh/getFlightInfoByCity',
				data: datas,
				success: function(res){
					if(res.data.result == 0){
						self.show4 = false
						uni.navigateTo({
							url: '../flight/flight?list=' + JSON.stringify(res.data.data) + '&date=' + self.date + '&day=' + self.day
						})
					}else{
						self.show4 = false
						Toast('暂无数据')
					}
				}
			}
			ajax(data)
		},
		goCount() {
			let self = this
			this.show4 = true
			let datas = {
				fnum: this.count,
				date: this.date
			}
			let data = {
				url: '/api/gzh/getFlightInfoByNum',
				data: datas,
				success: function(res){
					console.log(res)
					if(res.data.result == 0){
						self.show4 = false
						uni.navigateTo({
							url: '../flight/flight?list=' + JSON.stringify(res.data.data) + '&date=' + self.date + '&day=' + self.day
						})
					}else{
						self.show4 = false
						Toast('暂无该航班数据')
					}
				}
			}
			ajax(data)
		}
	}
}
